  // display font size extend for responsive
  @include media-breakpoint-up(sm) {
    .display-sm-1 {
      font-size: $display1-size;
    }
  
    .display-sm-2 {
      font-size: $display2-size;
    }
  
    .display-sm-3 {
      font-size: $display3-size;
    }
  
    .display-sm-4 {
      font-size: $display4-size;
    }
  
    .display-sm-5 {
