    }
  
    .display-sm-6 {
      font-size: $display6-size;
    }
  
    .display-sm-7 {
      font-size: $display7-size;
    }
  
    .display-sm-8 {
      font-size: $display8-size;
    }
  
  }
  
  @include media-breakpoint-up(md) {
    [class*='display-md-']{
      line-height: 1.2;
    }
  
    .display-md-1 {
      font-size: $display1-size;
    }
  
    .display-md-2 {
      font-size: $display2-size;
    }
  
    .display-md-3 {
